
*{
  margin: 0;
  padding: 0;
}

body{
  background: linear-gradient(277deg, rgba(0,0,0,1) 0%, rgba(115,115,115,1) 100%);
  text-align: center;
  color:whitesmoke;
}

h1{
  font-size: 3.1rem;
  font-weight:bold;
}

li{
  list-style: none;
}
a{
  color: whitesmoke;
  text-decoration: none;
}
 a:hover {
  font-size: 20px;
 }

.div-body{
  width:80vw;
  margin-top: 40px;
  margin-bottom: 100px;
  background: linear-gradient(277deg, rgba(0,0,0,1) 0%, rgba(115,115,115,1) 100%);
  height:140vh;
  box-shadow: 7px 8px 9px 0px whitesmoke;
  border:1px solid whitesmoke;
}

.ml14 {
  display: block;
  color:whitesmoke;
  text-align: center;
}

.ml14 .text-wrapper {
  position: relative;
  display: inline-block;
  padding-top: 0.1em;
  padding-right: 0.05em;
  padding-bottom: 0.15em;
}

.ml14 .line {
  opacity: 0;
  position: absolute;
  left: 0;
  height: 2px;
  width: 100%;
  background-color: whitesmoke;
  transform-origin: 100% 100%;
  bottom: 0;
}

.ml14 .letter {
  display: inline-block;
  line-height: 1em;
}


.ml15 {
  display: inline-block;
  color:whitesmoke;
  text-align: center;
}

.ml15 .text-wrapper {
  position: relative;
  display: inline-block;
  padding-top: 0.1em;
  padding-right: 0.05em;
  padding-bottom: 0.15em;
}

.ml15 .line {
  opacity: 0;
  position: absolute;
  left: 0;
  height: 2px;
  width: 100%;
  background-color: whitesmoke;
  transform-origin: 100% 100%;
  bottom: 0;
}

.ml15 .letter {
  display: inline-block;
  line-height: 1em;
}

nav{
  text-align: right;
}
.home-nav{
  margin-top: 300px;
}
.about-nav{
  margin-top: 110px;
}
.projects-nav{
  margin-top: 170px;
}
.contact-nav{
  margin-top: 190px;
}

.box1{
  width: 80%;
  height: 80%;
  position:relative;
  border-radius: 100%;
  background-size: cover;
}

 .about-content{
  text-align: left;
 }
 .about-grid{
  margin-top: 20px;
 }



.card{
  background-color: black;
  color:whitesmoke;
  border:1px solid whitesmoke;
}

 .card-text{
  font-size: 10px;
  width: 100%;

 }
 .card-link {
 color:whitesmoke;
 font-size: 10px;
 }

 .card-link:hover{
  color:blue;
  font-size: 10px;
 }

.see-more{
  border:1px solid whitesmoke;
  padding: 5px 20px;
  font-size: 10px;
  border-radius: 4px;
  position:relative;
  top:20px;
}
.see-more:hover{
  background-color: black;
  font-size: 10px;
}

 .contact-content{
  margin-top: 30px;
  max-width: 80%;
  padding: 3%;
  text-align: left;
  border:1px solid whitesmoke;
  background-color: black;
  font-size: 15px;

 }

 .contact-content a:hover{
  color:blue;
  font-size: 15px;
 }



 /* Media query for smaller devices */
@media (max-width: 600px) {
  .div-body{
    height:90vh;
  }
  h1{
    font-size: 2.0rem;
  }
   a:hover{
    color:blue;
   }
   .box1{
    display: none;
   }
   .about-content{
    font-size: 12px;
   }
}